import React,{useRef} from "react";

export const Field = ({ children, label, error,containerClassName }) => {
  const id = getChildId(children);
  const inputRef = useRef();

  return (
  
    <div className={containerClassName}  >
       
        <label  className="block tracking-wide text-gray-600 text-sm font-bold mb-2" htmlFor={id}>
                   {label}
        </label>
     
      {children}
      
      {error && <p className='text-red-500 text-xs italic'>{error.message} </p>}
    </div>
  );
};



// Get id prop from a child element
export const getChildId = (children) => {
  const cant=  React.Children.count(children);
 // console.log("cant."+ cant);

  const child = React.Children.only(children);
 
  if ("id" in child?.props) {
//    console.log(child.props.id);
    return child.props.id;
  }

};
