import React, { useRef } from 'react';

const Input =React.forwardRef((props, ref)=> {
    const {
        id,
   //     containerClassName = '',
        placeholder = '',
        className="peer p-2 rounded-xl border w-full  border-gray-300 text-gray-900  focus:border-indigo-500 focus:ring-indigo-500 ",
        label = '',
        type = 'text',
        error,
        //    errorText = '',
          ...rest
    } = props;

    const inputRef = useRef();

    return (
              
              

                <input
                    type={type}
                    ref={ref}
                    id={id}
                    name={id}
                    placeholder={placeholder}
                    className={`${className} ${
                        error ? "border border-red-500 " : ""
                        }`}         
                    aria-invalid={error ? "true" : "false"}
                    {...rest}
                />
        
            
      
    );
});

export default Input;