import { registerLocale } from "react-datepicker";
import es from "date-fns/locale/es";
import { useStepperContext } from "../../contexts/StepperContext";
import { useForm, Controller } from "react-hook-form";
import { useNavigate } from "react-router-dom";
import { useTranslation } from "react-i18next";

export default function Details() {
  const { userData, setUserData,currentStep, setCurrentStep} =useStepperContext();

  const { t } = useTranslation();
  registerLocale("es", es);
 
  
  // const handleChange = (e) => {
  //   const { name, value } = e.target;
  //   setUserData({ ...userData, [name]: value });
  // };
 
  const navigate = useNavigate();
  const handleRegistration = async (data) => {
      //   alert(JSON.stringify(data));
   //   await sleep(5000);
      console.log("data1", data);
     
      setUserData({
        ...userData,
       ...data
      });
     
      // data = JSON.stringify(values);
  
      alert( JSON.stringify(userData));
    //  handleClick("next");
      setCurrentStep(3);
      navigate("/step3");
    };

    const handleClick=()=>{
      setCurrentStep(1);
      navigate("/");
    }

  const {
    register,
    handleSubmit,
    watch,
    formState: { errors, isSubmitting },
    control,
  } = useForm({  defaultValues: userData, mode: "onSubmit" });
  return (

    <form className="w-full  " onSubmit={handleSubmit(handleRegistration)}>

    <div className="flex flex-col ">
      <div className="w-full mx-2 flex-1">
        <div className="font-bold h-6 mt-3 text-gray-500 text-xs leading-8 uppercase">
          Address
        </div>
        <div className="bg-white my-2 p-1 flex border border-gray-200 rounded">
          <input
           {...register("address1")}
        
            name="address1"
            placeholder="Address"
            className="p-1 px-2 appearance-none outline-none w-full text-gray-800"
          />
        </div>
      </div>
      <div className="w-full mx-2 flex-1">
        <div className="font-bold h-6 mt-3 text-gray-500 text-xs leading-8 uppercase">
          City
        </div>
        <div className="bg-white my-2 p-1 flex border border-gray-200 rounded">
          <input
     
          {...register("city1") }
            name="city1"
            placeholder="City"
            type="text"
            className="p-1 px-2 appearance-none outline-none w-full text-gray-800"
          />
        </div>
      </div>
     
    </div>
    <button
        onClick={handleClick}
        className={`cursor-pointer rounded-xl border-2 border-slate-300 bg-white py-2 px-4 font-semibold uppercase text-slate-400 transition duration-200 ease-in-out hover:bg-slate-700 hover:text-white  `}
      >
        Back
      </button>
    <button
       type="submit"
        className="bg-twitter text-white uppercase py-2 px-4 rounded-xl font-semibold cursor-pointer
      hover:bg-slate-700 hover:text-white transition duration-200 ease-in-out"
      >
        {t("Next")}
      </button>
    </form>
  );
}
